@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,400;0,800;1,400&display=swap);
body {
  margin: 0;
  font-family: "Raleway", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.button {
  background: linear-gradient(to bottom, rgb(212, 76, 59) 5%, #e4685d 100%);
  background-color: rgb(212, 76, 59);
  border-radius: 25px;
  border: none;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-size: 22px;
  padding: 20px 40px;
  margin-top: 20px;
  margin-bottom: 20px;
  text-decoration: none;
  text-shadow: 0px 0px 50px #b23e35;
  outline: none;
}
.button:hover {
  background: linear-gradient(to bottom, #e4685d 5%, #fc8d83 100%);
  background-color: #e4685d;
}
.button:active {
  position: relative;
  top: 1px;
}



nav{background:transparent}.navbar .nav-item{margin:20px;color:#f6f5f1;cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none}.navbar .nav-item:active,.navbar .nav-item:focus,.navbar .nav-item:hover{color:#d44c3b}.navbar-toggler{color:#d44c3b;border:none}

.about-wrapper{display:grid;grid-template-columns:repeat(auto-fit, minmax(350px, 1fr));background-color:#f6f5f1;padding-top:50px;padding-bottom:50px}.about-wrapper .about-image{justify-self:center;width:80%}.about-wrapper .about-text{justify-self:center;align-self:center;margin-right:250px;width:80%}@media only screen and (max-width: 716px){.about-text{text-align:center;margin-right:0px !important;width:80%}}

.business-wrapper{display:grid;background-color:#fff;padding:75px 0px;font-size:15px}.business-text{justify-self:center;grid-template-columns:repeat(auto-fit, minmax(350px, 1fr));margin-bottom:20px}.business-text h4{font-size:24px;margin:0 0 10px;padding:0 0 10px;position:relative;font-weight:bold}.business-text h4::after{background-color:#c5c5c5;bottom:0;content:"";display:block;height:3px;left:50%;position:absolute;transform:translate(-50%, 0);width:250px}.business-text p{width:75%;text-align:center;margin-left:12.5%}.business-cards{display:grid;grid-template-columns:repeat(auto-fit, minmax(350px, 1fr))}

.business-card-wrapper{justify-self:center;display:grid;grid-template-rows:1fr 1fr;justify-items:center;width:300px;height:120px;margin-top:50px;text-align:center}.business-card-wrapper .business-card-icon{color:red}.business-card-wrapper .title{font-weight:bold;font-stretch:expanded}

.scoreboard-wrapper{background-color:#f6f5f1;display:grid;grid-template-columns:repeat(auto-fit, minmax(350px, 1fr));font-size:15px;justify-items:center;text-align:center}

.scoreboard-card-wrapper{display:grid;grid-template-rows:1fr 1fr 1fr;justify-items:center;margin-top:70px;margin-bottom:30px}.scoreboard-card-wrapper .scoreboard-card-image{width:70px;height:70px;margin-bottom:30px}.scoreboard-card-wrapper .score{font-size:50px}

.home-wrapper{background-repeat:no-repeat;background-position:center;width:100%;height:1000px;z-index:-1;position:relative;text-align:center;color:white;text-shadow:0px 0px 10px rgba(0,0,0,0.67)}.home-wrapper .centered-text{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.home-wrapper .centered-text h1{font-size:60px;font-weight:bold}.home-wrapper .centered-text h3{font-style:oblique}.home-wrapper .centered-text h5{font-style:oblique}

.contact-wrapper{background-color:#f6f5f1;text-align:center}.contact-wrapper p{margin:0 30%;padding-top:30px}.contact-wrapper .button{outline:none}.contact-wrapper .social-media{font-size:22px;padding-bottom:20px}.contact-wrapper .social-media a{margin:25px;color:#d44c3b}.contact-wrapper .social-media a:hover{color:#212529}

.portfolio-item{background-position:center;background-size:cover;height:300px}

.container{padding:60px}.container .porfolio-text{padding-left:50px;padding-right:50px;padding-bottom:50px}.container h4{font-size:24px;margin:0 0 10px;padding:0 0 10px;position:relative;font-weight:bold}.container h4::after{background-color:#c5c5c5;bottom:0;content:"";display:block;height:3px;left:50%;position:absolute;transform:translate(-50%, 0);width:250px}.container p{text-align:center}.container .slider{margin-top:50px}.container .button{margin-top:0px;outline:none}.container .gallery{z-index:-1;transition:0.4s}

.testemonials-wrapper{margin-top:50px;margin-bottom:75px}.testemonials-wrapper h4{font-size:24px;margin:10px 0 10px;padding:0 0 10px;position:relative;font-weight:bold}.testemonials-wrapper h4::after{background-color:#c5c5c5;bottom:0;content:"";display:block;height:3px;left:50%;position:absolute;transform:translate(-50%, 0);width:250px}.testemonials-wrapper .carousel{margin-top:75px}.carousel-control-prev-icon,.carousel-control-next-icon{height:100px;width:100px;background-size:100%, 100%;border-radius:50%}.carousel-control-next-icon:after{content:">";font-size:55px;color:#d44c3b}.carousel-control-prev-icon:after{content:"<";font-size:55px;color:#d44c3b}.single-item{width:50%;text-align:center}.carousel-inner{text-align:center}

